export default {
	data() {
		return {
			dataList: [],
			page: 1
		}
	},
	components: {},
	onLoad() {
		this.loadData()
	},
	methods: {
		loadData() {
			let parmas = {
				nowPage: this.page
			};

			this._reqw.ipost(parmas, "helpList").then(res => {
				res.result == 0 ? this.dataList = res.dataList : this.$api.tip(
					res.resultNote)
			}).catch(err => {})
		},
		goto(e) {
			uni.navigateTo({
				url: `/pagesD/person/helpdetail?id=${e.id}`
			});
		}
	}
}
